  outline: 1px solid #757575;
  transform: opasity;
}

.client-logo-icon {
  width: 89px;
  height: 52px;
  fill: var(--light);
  transition: fill 250ms var(--timer);
}
.client-link:focus .client-logo-icon, .client-link:hover .client-logo-icon {
  fill: var(--accent-background);
  transform: opasity;
}

